.header {
    display: flex;
    margin-bottom: 60px;
    padding: 10px 40px;
}
.nav {
    flex-basis: 40%;
    margin-left: auto;
}
.nav-list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style: none;
    margin-left: auto;
    font-size: 26px;
}
.nav-list__item:not(:last-child) {
    margin-right: 20px;
}
@media screen and (max-width: 800px) {
    .nav {
        flex-basis: 100%;
    }
}
@media screen and (max-width: 500px) {
    .nav-list {
        justify-content: center;
        font-size: 19px;
    }
}